.container {
    display: flex;
    height: 90vh;
}

.subtitle {
    margin: 10px 0;
}

.left {
    width: 100%;
    background-image: linear-gradient(to right, #DBCEF2, #DAEBF3, #f1f1f1);
}

.description {
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
}

.right {
    width: 100%;
    background-image: linear-gradient(to left, #DBCEF2, #DAEBF3, #f1f1f1);
}