.technologies, .projects, .cv, .contact-me {
    text-align: center;
    margin: 5rem;
}

.icon {
    margin: 5rem;
    height: 8rem;
}

.tooltip {
    position: relative;
    display: inline-block;
    cursor: pointer;
    font-size: 16px;
}

.tooltip-text {
    visibility: hidden;
    width: 110px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    padding: 5px;
    position: absolute;
    z-index: 1;
    bottom: 80%;
    left: 50%;
    margin-left: -60px;
    opacity: 0;
    transition: opacity 0.3s;
}

.tooltip-text::after {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

.tooltip:hover .tooltip-text {
    visibility: visible;
    opacity: 1;
}

.cv-description {
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: space-around;
}

.description {
    padding: 4rem;
}